@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700;900&display=swap);
.container{width:100%;max-width:3000px;margin:0 auto}.container .row{width:95%;margin:0 auto;height:100%;display:flex;justify-content:center}.container .row [class*='col-']{min-height:1px}.container .row .col-1{width:8.33333%}.container .row .col-2{width:16.66667%}.container .row .col-3{width:25%}.container .row .col-4{width:33.33333%}.container .row .col-5{width:41.66667%}.container .row .col-6{width:50%}.container .row .col-7{width:58.33333%}.container .row .col-8{width:66.66667%}.container .row .col-9{width:75%}.container .row .col-10{width:83.33333%}.container .row .col-11{width:91.66667%}.container .row .col-12{width:100%}*{box-sizing:border-box;margin:0;padding:0}body{font-family:"Lato",sans-serif;font-weight:400;position:relative}a{text-decoration:none;color:#000}

.hero{background-color:grey;position:absolute;height:100vh;width:100%;background-position:center;background-size:cover;-webkit-filter:brightness(60%);filter:brightness(60%);z-index:-1}

.fullForm,.smallForm{height:100vh}.fullForm .header,.smallForm .header{flex-direction:column;color:#fff}.fullForm .header .header__title,.smallForm .header .header__title{font-size:2em;line-height:2em;font-weight:900}.fullForm .header .header__text,.smallForm .header .header__text{margin:5px 0;text-align:justify}.fullForm .header .header__text:last-of-type,.smallForm .header .header__text:last-of-type{font-size:0.9em;text-align:center;line-height:2em}.fullForm .header .header__text a,.smallForm .header .header__text a{color:#fff;text-decoration:underline;background-image:url()}.fullForm .header .header__form,.smallForm .header .header__form{display:flex;align-items:center;position:relative;margin:20px 0}.fullForm .header .header__form .fas,.smallForm .header .header__form .fas{background-color:#fff;color:grey;position:absolute}.fullForm .header .header__form .fa-search,.smallForm .header .header__form .fa-search{left:15px}.fullForm .header .header__form .fa-times,.smallForm .header .header__form .fa-times{right:15px}.fullForm .header .header__form .header__form__input,.smallForm .header .header__form .header__form__input{width:100%;height:40px;padding:10px 10px 10px 50px}.fullForm .header .header__form .header__form__label,.smallForm .header .header__form .header__form__label{position:absolute;font-weight:700;color:#ab0000;top:-65px;left:calc(50% - 120px);font-size:2em;line-height:2em}.fullForm .header .header__form .header__form__input--error,.smallForm .header .header__form .header__form__input--error{border-color:#ab0000;color:#ab0000;outline:#ab0000}.fullForm .header .header__form--error,.smallForm .header .header__form--error{margin-top:65px}.smallForm{height:auto}.smallForm .header{width:75%}.smallForm .header .header__form{margin-bottom:0}.smallForm .header .header__form .fas{background-color:#e6e6e6}.smallForm .header .header__form .header__form__input{border-radius:20px;border:none;outline:none;background-color:#e6e6e6}.smallForm .header .header__form .header__form__input:focus{border:2px solid black;padding-left:48px}

.photoGallery{margin:0 auto 20px}.photoGallery .photoGallery__description{justify-content:flex-start;overflow-x:hidden}.photoGallery .photoGallery__description .photoGallery__description__title{margin-left:5px;font-weight:700;font-size:2em;line-height:2em}.photoGallery .photoGallery__description .photoGallery__description__tag{margin:5px 5px 30px 5px;padding:5px 30px;border:1px solid #e6e6e6;color:grey;display:flex;align-items:center;flex:1 0 auto;cursor:pointer;border-radius:4px}.photoGallery .photoGallery__description .photoGallery__description__tag:hover{background-color:#e6e6e6}.photoGallery .photoGallery__list{flex-wrap:wrap}

.photoGallery__list__elem{list-style:none;margin:0 5px 10px 5px;flex-grow:1;height:25vh}.photoGallery__list__elem:last-child{flex-grow:10}.photoGallery__list__elem .list__elem__img{max-height:100%;min-height:100%;max-width:100%;min-width:100%;object-fit:cover;vertical-align:bottom;border-radius:4px;transition:transform 300ms}.photoGallery__list__elem .list__elem__img:hover{transform:scale(1.02);cursor:pointer}.photoGallery__list__elem .list__elem__container{position:absolute;top:0;left:0;padding-top:10vh;overflow-x:hidden;width:100%;height:100%;background-color:rgba(0,0,0,0.7);z-index:1000;display:flex;flex-direction:column;align-items:center}.photoGallery__list__elem .list__elem__container .elem__container__user{display:flex;justify-content:center;align-items:center;width:90vw;background-color:#fff;padding-top:2%}.photoGallery__list__elem .list__elem__container .elem__container__user img{margin:0 5px}.photoGallery__list__elem .list__elem__container .elem__container__user p{text-align:center}.photoGallery__list__elem .list__elem__container .elem__container__img{display:block;max-width:90vw;max-height:80vh;background-size:cover;padding:2%;background-color:#fff}

